.parallax {
    width: 100vw;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    height: 50vh;
    /* background-position: center; */
    /* background-repeat: no-repeat; */
    /* background-size: cover; */
    margin: 0px 0px;
    z-index: -1;
    background-color: whitesmoke;
    overflow: hidden;
    -ms-flex-line-pack: center;
        align-content: center;
    margin: auto;

}

.parallaxImage {
    z-index: -1;
    position: absolute;
    top: 5vh;
    /* right: 50%; */
    margin: 0px 0px; 
    position: relative;
    height: 100vh;
    width: 100vw;
    min-width: 1440px;
    overflow: hidden;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    -ms-flex-line-pack: center;
        align-content: center;
    margin: auto;
}


